body{font-size:16px}body #wrapper{min-height:100vh;display:flex;justify-content:center;align-items:center}body #wrapper #form-login{max-width:400px;background:rgba(0,0,0,.8);flex-grow:1;padding:30px 30px 40px;box-shadow:0px 0px 17px 2px rgba(255,255,255,.8)}body #wrapper #form-login .form-heading{font-size:25px;color:#f5f5f5;text-align:center;margin-bottom:30px}body #wrapper #form-login .form-group{border-bottom:1px solid #fff;margin-top:15px;margin-bottom:20px;display:flex}body #wrapper #form-login .form-group i{color:#fff;font-size:14px;padding-top:5px;padding-right:10px}body #wrapper #form-login .form-group .form-input{background:rgba(0,0,0,0);border:0;outline:0;color:#f5f5f5;flex-grow:1}body #wrapper #form-login .form-group .form-input:placeholder{color:#f5f5f5}body #wrapper #form-login .form-group #eye i{padding-right:0;cursor:pointer}body #wrapper #form-login .form-submit{background:rgba(0,0,0,0);border:1px solid #f5f5f5;color:#fff;width:100%;text-transform:uppercase;padding:6px 10px;transition:.25s ease-in-out;margin-top:30px}body #wrapper #form-login .form-submit:hover{border:1px solid #54a0ff;cursor:pointer}body #wrapper .errmsg{background-color:#ffb6c1;color:#b22222;font-weight:bold;padding:.5rem;margin-bottom:.5rem;font-size:15px}body #wrapper .offscreen{display:none}body #wrapper .form-register-forgot-pass .register,body #wrapper .form-register-forgot-pass .forgot-pass{font:20px}body #wrapper .form-register-forgot-pass .register{float:right}