.Header{display:flex;justify-content:space-between;align-items:center;height:80px;width:100%;flex:0 1 auto;background-color:#333;color:#fff}.Header .logo{margin-left:50px}.Header .logo img{-o-object-fit:contain;object-fit:contain;max-height:80px;max-width:50px}.Header .place-holder{align-content:center;width:300px;display:none}.Header .user{height:100%;padding:20px;justify-content:space-between;align-content:center;text-align:center;box-sizing:border-box;background-color:#333}.Header .user .notification-icon{margin-top:5px;display:inline-block;padding:5px;cursor:pointer}.Header .user .notification-icon,.Header .user .user-menu{margin:0px 10px 0px 10px;font-size:15px}.Header .user a{color:#f5f5f5}.Header .user .user-menu{display:inline-block;align-items:center;color:#f5f5f5}.Header .user .user-menu .user-icon{cursor:pointer;border:1px solid #fff;padding:7px;border-radius:50%}.Header .user .user-menu .dropdown-menu{position:relative;border-radius:5px}.Header .user .user-menu .dropdown-menu ul{background-color:#333;list-style:none;padding:0;margin:0;position:absolute;top:100%;right:0;box-shadow:0 2px 4px rgba(0,0,0,.1);display:none}.Header .user .user-menu .dropdown-menu ul li{padding:10px}.Header .user .user-menu .dropdown-menu ul li a{text-decoration:none;color:#f5f5f5;display:block}.Header .user .user-menu:hover .dropdown-menu ul{display:block}